@import url(https://fonts.googleapis.com/css2?family=Spartan:wght@500;700&display=swap);
.cardList{display:flex;flex-wrap:wrap}.loading{margin-top:20vw;text-align:center;font-weight:700;font-size:40px}.header{width:100vw;height:7rem;background-image:url(data:image/png;base64,);background-position:center center;background-repeat:no-repeat;background-color:hsl(157, 65%, 92%);background-color:var(--primary-color)}:root{--primary-color:hsl(157, 65%, 92%);--background-color:hsl(180, 52%, 96%);--filter-table:hsl(180, 31%, 95%);--dark-greyish:hsl(180, 8%, 52%);--very-dark-greyish:hsl(180, 14%, 20%);--dark-green:rgb(10, 131, 78);--light-green:rgb(75, 167, 127)}*{margin:0;padding:0;box-sizing:inherit}html{font-size:15px;font-weight:500;box-sizing:border-box}body{font-family:'Spartan', sans-serif;background-color:hsl(180, 52%, 96%);background-color:var(--background-color)}@media only screen and (max-width: 375px){html{font-size:0.75rem}}.dogCard{display:flex;flex-direction:column;align-items:center;position:relative;width:200px;height:325px;background-color:#fff;padding:0rem 6rem 10rem;margin:20px;border-radius:25px;box-shadow:0px 4px 6px 4px rgba(91,164,164,0.2)}.dogCard:hover{box-shadow:0px 4px 6px 4px rgba(50,90,90,0.2)}@media only screen and (max-width: 375px){.dogCard{flex-direction:column;width:200px;padding-bottom:1rem;margin-bottom:2rem}}.dogCard img{width:200px;height:200px;position:inherit;border-top-left-radius:25px;border-top-right-radius:25px}.dogCard .cardTitle{position:relative;font-weight:700;color:hsl(180, 14%, 20%);color:var(--very-dark-greyish);word-wrap:break-word;font-size:15px;width:180px;text-align:center}.dogCard .cardAdditional{margin-top:0.5rem;color:hsl(180, 8%, 52%);color:var(--dark-greyish);font-size:10px;width:200px;text-align:center}.dogCard .cardButtonPart{margin-top:0.7rem;display:inherit;width:100px;border:1px solid rgb(10, 131, 78);border:1px solid var(--dark-green);background-color:rgb(10, 131, 78);background-color:var(--dark-green);border-radius:10px;padding:10px;position:relative;white-space:nowrap}.dogCard .cardButtonPart:hover{border-color:rgb(75, 167, 127);border-color:var(--light-green);background-color:rgb(75, 167, 127);background-color:var(--light-green)}.dogCard .cardButtonPart span{font-weight:300;margin:auto;color:white}

